* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fefefe;
  color: #212121;
  line-height: 1.5;
}

img {
  display: inline-block;
  width: 100%;
  height: auto;
}

button {
  font-family: inherit;
  margin: 0;
  padding: 0;
}

ul {
  list-style: none;
  margin: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

.load-more {
  font-family: inherit;
  display: inline-block;
  text-align: center;
  height: 40px;
  width: 140px;
  margin-left: 40%;

  border: none;
  border-radius: 12px;
  background-color: #00bfff;
  color: #fff;
}

.load-more:focus,
.load-more:hover {
  background-color: #1e90ff;
  box-shadow: inset 3px 3px 3px #1e90ffgfz;
}

.load-more.is-hidden {
  display: none;
}

.search-form {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 10px;
  padding-bottom: 10px;
}

.search-form > input {
  border-radius: 4px;
  padding: 4px 0 4px 4px;
  width: 300px;
}

.search-form > button {
  font-weight: bold;
  display: inline-block;
  height: 20px;
  width: 25px;
  transform: translateX(-28px);
  border: none;
  cursor: pointer;
}

.header {
  width: 100%;
  background-color: #00bfff;
}

.gallery {
  display: flex;
  justify-content: center;
  flex-flow: row wrap;
  padding: 10px;
}

.photo-card {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  flex-basis: calc(100% / 4 - 80px);
  margin: 10px;
  outline: 1px solid black;
}

.info {
  padding: 0 10px 0 10px;
}


